/**
 * Estilização do app
 */
.reset-button,
button,
.navbar-toggle {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.clearfix:after,
.navbar-control:after {
  content: "";
  display: table;
  clear: both;
}
@font-face {
  font-family: 'ebenezer';
  src: url("../fonts/ebenezer.eot");
  src: url("../fonts/ebenezer.eot#iefix") format('embedded-opentype'), url("../fonts/ebenezer.woff") format('woff'), url("../fonts/ebenezer.ttf") format('truetype'), url("../fonts/ebenezer.svg#ebenezer") format('svg');
  font-weight: normal;
  font-style: normal;
}
@import url("https://fonts.googleapis.com/css?family=Montserrat:700|Open+Sans:400,700");
[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "ebenezer";
  font-style: normal;
  font-weight: normal;
  speak: none;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: 0.2em;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  line-height: 1em;
  margin-left: 0.2em;
  font-size: 120%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 1px 1px 1px rgba(127,127,127,0.3);
}
* {
  box-sizing: border-box;
}
html {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
}
::selection {
  background: #7d965d;
  color: #fff;
}
:focus {
  border: 0;
  outline: 2px auto #333;
}
h1 {
  margin-bottom: 30px;
  font-family: 'Montserrat';
  font-size: 24px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
}
ul {
  padding: 0;
  margin: 0;
}
li {
  list-style: none;
  margin-bottom: 0;
}
._img-responsive {
  max-width: 100%;
  display: block;
  height: auto;
}
._text-right {
  text-align: right;
}
._overflow-hidden {
  overflow-x: hidden;
}
._section-site {
  margin-top: 20px;
}
._center-block {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
._btn-margin {
  margin-top: 40px;
}
@media (min-width: 1200px) {
  .__invisible-lg {
    display: none !important;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .__invisible-md {
    display: none !important;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .__invisible-sm {
    display: none !important;
  }
}
@media (min-width: $screen-xs-min) and (max-width: 767px) {
  .__invisible-vs {
    display: none !important;
  }
}
@media (max-width: $screen-xs-min -1) {
  .__invisible-xs {
    display: none !important;
  }
}
.btn-default {
  padding: 8px 35px;
  font-weight: bold;
  color: #7e9659;
  border-radius: 20px;
  border: 2px solid;
  border-color: #7e9659;
  transition: 0.2s all linear;
  display: inline-block;
}
.btn-default:hover,
.btn-default:focus,
.btn-default:active {
  background: #7e9659;
  color: #fff;
  outline: 0;
}
a.btn-default {
  text-decoration: none;
}
a.btn-default:hover,
a.btn-default:focus,
a.btn-default:active {
  text-decoration: none;
}
@media (min-width: 1200px) {
  .container-nav {
    margin-top: 90px;
    overflow: hidden;
  }
}
.header-site {
  background-color: #e1e3da;
  padding-top: 15px;
}
.jump-to {
  position: absolute;
  z-index: 999999;
}
.jump-to > .link {
  width: 1px;
  height: 1px;
  padding: 5px;
  left: -10000px;
  background-color: #7d965d;
  position: absolute;
  overflow: hidden;
}
.jump-to > .link:focus,
.jump-to > .link:active,
.jump-to > .link:hover {
  width: auto;
  height: auto;
  left: 0;
  color: #fff;
  text-decoration: none;
  position: static;
  overflow: visible;
}
.logo-site {
  display: inline-block;
  padding-bottom: 15px;
}
.owl-buttons {
  width: 100%;
  height: 0;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  margin: auto;
}
.owl-buttons .icon {
  font-size: 70px;
  color: #fff;
}
.owl-buttons .icon:hover,
.owl-buttons .icon:focus,
.owl-buttons .icon:active,
.owl-buttons .icon:active:focus {
  color: #7d965d;
  transition: all 0.1s ease-in-out;
  opacity: 0.8;
}
.owl-buttons .owl-prev,
.owl-buttons .owl-next {
  top: -35px;
  position: absolute;
}
.owl-buttons .owl-prev {
  left: 0;
}
.owl-buttons .owl-next {
  right: 0;
}
.owl-pagination {
  width: 100%;
  position: absolute;
  bottom: 10px;
  text-align: center;
}
.owl-pagination .owl-page {
  display: inline-block;
  margin-right: 10px;
}
.owl-pagination .owl-page span {
  background-color: #fff;
  width: 15px;
  height: 15px;
  display: inline-block;
  border-radius: 50%;
}
.owl-pagination .owl-page:hover span,
.owl-pagination .owl-page.active span {
  background-color: #7d965d;
}
.nav-site > .item > .link {
  font-size: 14px;
  font-weight: bold;
  color: #333;
  text-decoration: none;
  text-transform: uppercase;
  position: relative;
  display: block;
}
@media (min-width: 1200px) {
  .nav-site {
    text-align: right;
  }
  .nav-site > .item {
    display: inline-block;
    margin-right: 15px;
  }
  .nav-site > .item:last-child {
    margin-right: 0;
  }
  .nav-site > .item > .link {
    padding-bottom: 15px;
  }
  .nav-site > .item > .link:after {
    content: '';
    width: 100%;
    height: 6px;
    bottom: 0;
    left: 0;
    background-color: #848973;
    transform: translateY(7px);
    transform-origin: bottom;
    transition: 0.2s all linear;
    position: absolute;
  }
  .nav-site > .item > .link:hover,
  .nav-site > .item > .link:focus,
  .nav-site > .item > .link:active {
    color: #848973;
    outline: 0;
  }
  .nav-site > .item > .link:hover:after,
  .nav-site > .item > .link:focus:after,
  .nav-site > .item > .link:active:after {
    transform: translateY(0);
  }
}
.navbar-control {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.navbar-control:not(.-close) {
  margin-top: 75px;
  margin-left: 20px;
}
.navbar-control > .text {
  margin: 0 5px 0 0;
  font-size: 14px;
  color: #333;
}
.navbar-toggle {
  width: 52px;
  height: 40px;
  padding: 9px 10px;
  position: relative;
}
.navbar-toggle > .icon-bar {
  display: block;
  width: 30px;
  height: 3px;
  border-radius: 4px;
  background-color: #333;
  transition: all 0.2s linear;
}
.navbar-toggle > .icon-bar:nth-child(2) {
  opacity: 1;
}
.navbar-toggle .icon-bar + .icon-bar {
  margin-top: 4px;
}
.sr-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
.wrapper-site {
  transition: 0.4s transform ease-in-out;
}
.wrapper-site.js-active-menu {
  height: 100%;
  transition: 0.4s transform ease-in-out;
  transform: translateX(-200px);
}
.wrapper-site.js-active-menu .navbar-toggle > .icon-bar:nth-child(1) {
  transform: rotate(45deg);
}
.wrapper-site.js-active-menu .navbar-toggle > .icon-bar:nth-child(2) {
  width: 0;
  height: 0;
  margin: 0;
  opacity: 0;
}
.wrapper-site.js-active-menu .navbar-toggle > .icon-bar:nth-child(3) {
  margin-top: -3px;
  transform: rotate(-45deg);
}
@media (max-width: 1199px) {
  .js-ie .wrapper-site.js-active-menu .nav-site {
    transition: 0.4s transform ease-in-out;
    transform: translateX(0px);
    opacity: 1;
  }
  .js-ie .nav-site {
    overflow-x: hidden;
    overflow-y: auto;
    transition: 0.4s transform ease-in-out;
  }
  .js-ie .nav-site._no-opacity {
    opacity: 0;
  }
  .nav-site {
    width: 200px;
    height: 100%;
    min-height: 100vh;
    min-height: 100vmax;
    top: 0;
    right: 0;
    background-color: #e1e3da;
    transform: translateX(200px);
    visibility: hidden;
    position: fixed;
  }
  .nav-site.js-active {
    visibility: visible;
  }
  .nav-site > .item > .link {
    padding: 15px;
  }
  .nav-site > .item > .link:hover,
  .nav-site > .item > .link:focus,
  .nav-site > .item > .link:active {
    color: #fff;
    background-color: #848973;
    outline: 0;
  }
}
